    height: 100vh;
    background-size: cover;
    background-position: center;
    background-image: linear-gradient(rgba(4, 9, 30, 0.7), rgba(4, 9, 30, 0.7)),
    url(../../Assets/ice-bg.jpg);
    position: relative;
}

@media screen and (max-width: 800px) {
    .ice {
        height: 80vh;
    }
}