.about {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-image: linear-gradient(rgba(4, 9, 30, 0.7), rgba(4, 9, 30, 0.7)),
    url(../../Assets/About-bg.jpg);
    position: relative;
}

.simple-txt h1 {
    font-size: 55px;
    margin-top: 10%;
}

.info-abt h3, .info-abt h1, .info-abt p {
    color: #0047AB;
}

.info-abt h1 {
    margin: 25px 0;
    text-align: center;
}

.info-abt h3 {
    margin: 25px 0;
}

.info-abt {
    margin-bottom: 55px;
}

@media screen and (max-width: 800px) {
    .about {
        height: 80vh;
    }
    
    .simple-txt h1 {
        font-size: 30px;
        margin-bottom: 20px;
    }

    .info-abt h1 {
        font-size: 30px;
    }

    .info-abt p {
        font-size: 18px;
    }
    
}