.contact {
    margin-top: 40px;
    display: flex;
    gap: 50px;
}

.title h1 {
    color: #0047AB;
    font-size: 55px;
    text-align: center;
    margin-bottom: 35px;
}

form input, form textarea {
    width: 100%;
    border: 0;
    outline: none;
    background: lightgray;
    padding: 15px;
    margin: 15px 0;
    color: black;
    font-size: 15px;
    border-radius: 6px;
}

.form-c button {
    height: 60px;
    width: 205px;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
    font-size: 18px;
    border: #0047AB;
    background-color: #0047AB;
    color: #fff;
}

.message {
    margin: 45px 0;
}

.message h1, .message p {
    color: #0047AB;
    text-align: center;
    margin: 20px 0;
}

@media screen and (max-width: 800px) {
    .title h1, .message h1 {
        font-size: 30px;
    }

    iframe {
        width: auto;
    }

    .contact {
        display: inline-block;
    }

    form input, form textarea {
        width: 80%;
    }

    .form-c {
        text-align: center;
    }
}